.who {
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url(./Aguada.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  flex-wrap: wrap;
  align-content: center;
  height: 100vh;
  width: 100%;
}

.contTitle {
  width: 40%;
  text-align: left;
  margin: 0 auto;
}
.contImg {
  width: 50%;
  text-align: center;
  margin: 0 auto;
}

.office {
  width: auto;
  height: 300px;
  border-radius: 20px;
}

.title1 {
  color: whitesmoke;
  font-size: 2rem;
}

.para1 {
  color: whitesmoke;
  font-size: 1.2rem;
  text-align: left;
  line-height: 1.2;
}

@media only screen and (max-width: 1038px) {
  .contTitle {
    width: 90%;
    margin: 2rem 0;
  }
  .contImg {
    width: 900%;
  }
}

@media only screen and (max-width: 546px) {
  .contTitle {
    width: 90%;
    margin: 2rem 0;
  }
  .office {
    width: 90%;
    height: 100%;
  }
}
