footer {
  background-color: rgba(201, 201, 211, 0.349);
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  height: auto;
  width: 100%;
  color: black;
  padding: 2rem;
  flex-wrap: wrap;
}

footer h3,
.iconsss {
  font-size: 1.3rem;
}

.copyRight {
  width: 20%;
}

.iconsss {
  width: 60%;
  display: flex;
  justify-content: space-around;
}

.iconsss a {
  width: 7%;
  text-align: center;
  border-radius: 1rem;
}

.iconsss a:hover {
    background-color: rgba(132, 186, 236, 0.5);
    transition: 1s;
}

.fas,
.fab {
  color: black;
}

@media only screen and (max-width: 728px) {
  .iconsss h3 {
    display: none;
  }
  .iconsss {
    width: 100%;
  }

  .copyRight {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .iconsss h3 {
    display: none;
  }
}
