.header {
    height: 3rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom: 1px solid rgb(216, 205, 205);
    position: fixed;
    width: 100%;
    top: 0;
    background-color: white;
}

.logo {
    width: 40%;
    margin: 0 30% 0 5%;
}

.navBar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 60%;
    list-style: none;
}

.navBarMobile li {
    list-style: none;
}

.line {
    background-color: black;
    margin: 3px 0 4px 0;
    width: 25px;
    height: 4px;
    display: none;
}

.navLink {
    text-decoration: none;
    color: black;
    border-radius: 1rem;
    padding: 0.3rem;
}

.navLink:hover {
    text-decoration: none;
    background-color: rgba(132, 186, 236, 0.5);
    transition: 1s;
}

.navTextContainerMobile {
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    opacity: 97%;
    z-index: 10;
    background-color: white;
}

.navTextContainerMobile li,
.navTextContainerMobile button {
    color: black;
    margin: 0 auto;
    list-style: none;
    font-size: 1rem;
}

.navTextContainerMobile li a {
    text-decoration: none;
    color: black;
    padding: 0.5rem;
}

.navTextContainerMobile li a:hover {
    text-decoration: none;
    color: black;
    background-color: rgba(19, 131, 235, 0.5);
    transition: 0.5s;
    border-radius: 2rem;
}

.navTextContainerMobile button {
    width: 20%;
}

@media only screen and (max-width: 816px) {
    .logo {
        width: 20%;
    }
    .navBar {
        width: 90%;
    }
}

@media only screen and (max-width: 665px) {
    .line {
        display: block;
    }
    .navBar {
        display: none;
    }
}