.SectionFirst {
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url(./Aguada.jpg);
  background-attachment: fixed;
  height: 600px;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.containerQuote {
  margin: 0 5%;
  height: auto;
}
.mainQuote {
  font-size: 4rem;
  color: white;
}

.containerButtom {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: auto;
  width: auto;
  margin: 0 14%;
  font-size: 2rem;
}

.containerButtom a {
  padding: 0.6rem;
  text-decoration: none;
  color: white;
  border-radius: 2rem;
}

.buttom1:hover,
.buttom1:visited,
.buttom2:hover,
.buttom2:visited {
  background-color: rgba(230, 230, 250, 0.349);
  transition: 1.5s;
}

.cards {
  height: auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 3rem 0 3rem 0;
  width: 90%;
  font-size: 1rem;
}

.card1,
.card2,
.card3,
.card4,
.card5,
.card6 {
  box-shadow: 5px 5px 5px 5px rgba(190, 190, 196, 0.349);
  height: 280px;
  text-align: center;
  padding: 40px;
  margin: 2%;
  border-radius: 20px;
  display: flex;
  justify-content: flex-start;
  border: solid 1px black;
  align-items: center;
  flex-direction: column;
  flex-basis: 300px;
}

.card1 i,
.card2 i,
.card3 i,
.card4 i,
.card5 i,
.card6 i {
  color: royalblue;
  align-self: baseline;
}



.card1 i,
.card2 i,
.card3 i,
.card4 i,
.card5 i,
.card6 i,
.cardHeader,
.cardPara {
  font-size: 1.2rem;
}

.card1:hover,
.card2:hover,
.card3:hover,
.card4:hover,
.card5:hover,
.card6:hover {
  background-color: rgba(132, 186, 236, 0.5);
  transition: 0.5s;
}

@media only screen and (max-width: 685px) {
  .containerQuote {
    width: 100%;
    margin: 0;
  }
  .mainQuote {
    text-align: center;

    font-size: 40px;
    margin: 0 auto;
  }
  .containerButtom {
    margin: 0 auto;
    text-align: center;
  }

  .buttom1,
  .buttom2 {
    font-size: 20px;
  }
}

@media only screen and (max-width: 348px) {
  .card1 i,
  .card2 i,
  .card3 i,
  .card4 i,
  .card5 i,
  .card6 i,
  .cardHeader,
  .cardPara {
    font-size: 1rem;
  }
}
