.reset {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.cont {
  background-image: url(./Aguada.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.705), rgba(0, 0, 0, 0.705)),
    url(./Aguada.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 50px;
  text-align: center;
}
.containerStaff {
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  border-radius: 1rem;
  width: 95%;
  margin: 10px 0;
  color: black;
  background-color: whitesmoke;
  padding: 1rem;
}

.profileUser {
  height: 200px;
  width: 200px;
  border-radius: 10rem;
}

.names,
.reporTo,
.jobDescription {
  width: 200px;
}

@media only screen and (max-width: 880px) {
  .names,
  .reporTo,
  .jobDescription {
    width: 100%;
  }
}
